import { useState } from "react";
import "../css/App.css";

function MembersDecision({ loginStatus }) {
  const [member1, setMember1] = useState("");
  const [member2, setMember2] = useState("");
  const [member3, setMember3] = useState("");
  const [member4, setMember4] = useState("");

  let body = {
    id: localStorage.getItem("ID"),
    test_num: localStorage.getItem("test_num"),
  };

  const interval = setInterval(() => {
    let fMD = localStorage.getItem("fetchMemberDecision");
    if (fMD) {
      fetch(
        "https://server-real.herokuapp.com/membersdecision",
        //"http://localhost:4000/membersdecision",
        {
          method: "post", // 
          headers: {
            "content-type": "application/json",
          },
          body: JSON.stringify(body),
        }
      )
        .then((response) => response.json())
        .then((res) => {
          if (res) {
            for (let i = 0; i < res.length; i++) {
              if (res[i]["user_id"] === localStorage.getItem("ID")) {
                res.splice(i, 1);
              }
            }
            setMember1(res[0][`init_price_${body.test_num}`]);
            setMember2(res[1][`init_price_${body.test_num}`]);
            setMember3(res[2][`init_price_${body.test_num}`]);
            setMember4(res[3][`init_price_${body.test_num}`]);
          }
        });
    }
  }, 5000);

  return (
    <div>
      <div style={{ height: "140px" }}></div>

      <div style={{ top: "6%", position: "relative" }}>
        <div
          className="group-left-box"
          style={{
            border: "1px solid",
            padding: "10px",
            top: "15%",
            left: "20%",
            width: "25%",
            position: "relative",
            float: "left",
          }}
        >
           1  : {member1}
        </div>
        <div
          className="group-right-box"
          style={{
            border: "1px solid",
            padding: "10px",
            top: "15%",
            left: "30%",
            width: "25%",
            position: "relative",
            float: "left",
          }}
        >
           2  : {member2}
        </div>

        <div style={{ height: "140px" }}></div>

        <div
          className="group-left-box"
          style={{
            border: "1px solid",
            padding: "10px",
            top: "15%",
            left: "20%",
            width: "25%",
            position: "relative",
            float: "left",
          }}
        >
           3  : {member3}
        </div>
        <div
          className="group-right-box"
          style={{
            border: "1px solid",
            padding: "10px",
            top: "15%",
            left: "30%",
            width: "25%",
            position: "relative",
            float: "left",
          }}
        >
           4  : {member4}
        </div>
      </div>
    </div>
  );
}

export default MembersDecision;
