import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";

function RemainingTimeAndInitialPrice({ onNextPage }) {
  const [time, setTime] = useState(180);
  const [initialPrice, setInitialPrice] = useState();

  const onInitialPriceHandler = (event) => {
    setInitialPrice(event.currentTarget.value);

    let body = {
      id: localStorage.getItem("ID"),
      test_num: localStorage.getItem("test_num"),
      initialPrice: event.currentTarget.value,
      predTime: 180 - time,
    };

    fetch(
      "https://server-real.herokuapp.com/initialPrice",
      //"http://localhost:4000/initialPrice",
      {
        method: "post", // 
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify(body),
      }
    );
  };

  const onSubmitHandler = (event) => {
    //       .
    event.preventDefault();

    if (initialPrice === "") {
      return alert(" .");
    }

    let body = {
      id: localStorage.getItem("ID"),
      test_num: localStorage.getItem("test_num"),
      initialPrice: initialPrice,
      predTime: 180 - time,
    };

    fetch(
      "https://server-real.herokuapp.com/initialPrice",
      //"http://localhost:4000/initialPrice",
      {
        method: "post", // 
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify(body),
      }
    )
      .then((response) => response.json())
      .then((res) => {
        alert(" .");

        onNextPage();
      });
  };

  let temp = "off";

  const onClickStopTimer = () => {
    if (temp == "off") {
      temp = "on";
    } else {
      temp = "off";
    }
    let interval = setInterval(() => {
      if (temp == "on") {
        setTime((realTime) => 20);
      }
    }, 1000);
  };

  const onClickTempNext = () => {
    onNextPage();
  };

  useEffect(() => {
    if (time > 0) {
      const timer = window.setInterval(() => {
        setTime((prevTime) => prevTime - 1); // <-- Change this line!
      }, 1000);
      return () => {
        window.clearInterval(timer);
      };
    } else {
      onNextPage();
    }
  }, [time]);

  return (
    <div style={{ top: "75%", left: "32%", position: "absolute" }}>
      <p>
        Please suggest the price of a coin at which your team should sell within
        3 minutes
      </p>
      Remaining Time: {time}
      <form
        style={{ display: "flex", flexDirection: "column" }}
        onSubmit={onSubmitHandler}
      >
        <h3 style={{ textAlign: "center" }}>Initial Price</h3>
        <input
          type="text"
          value={initialPrice}
          onChange={onInitialPriceHandler}
          className="input-box"
        />
        <div className="blank-box"></div>
        <button type="submit" className="button">
          Submit
        </button>
      </form>
      <button onClick={onClickStopTimer}>Stop Timer</button>
      <button onClick={onClickTempNext}>Temp next page</button>
    </div>
  );
}
export default RemainingTimeAndInitialPrice;
