import { useState } from "react";
import "../css/App.css";

function CoinAndCash({ loginStatus }) {
  const [coin, setCoin] = useState("");
  const [cash, setCash] = useState("");

  let body = {
    id: localStorage.getItem("ID"),
  };

  fetch(
    "https://server-real.herokuapp.com/coinandcash",
    //"http://localhost:4000/coinandcash",
    {
      method: "post", // 
      headers: {
        "content-type": "application/json",
      },
      body: JSON.stringify(body),
    }
  )
    .then((response) => response.json())
    .then((res) => {
      if (res) {
        setCoin(res[0]["num_of_coins"]);
        setCash(res[0]["amount_of_cash"]);
      }
    });

  return (
    <div>
      <div
        className="group-left-box"
        style={{
          border: "1px solid",
          padding: "10px",
          top: "15%",
          left: "20%",
          width: "25%",
          position: "relative",
          float: "left",
        }}
      >
           : {coin}
      </div>
      <div
        className="group-right-box"
        style={{
          border: "1px solid",
          padding: "10px",
          top: "15%",
          left: "30%",
          width: "25%",
          position: "relative",
          float: "left",
        }}
      >
             : {cash}
      </div>
    </div>
  );
}

export default CoinAndCash;
