import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import "../css/App.css";

function Register() {
  // react hook state 
  const [Email, setEmail] = useState("");
  const [Password, setPassword] = useState("");
  const [Name, setName] = useState("");
  const [ConfirmPassword, setConfirmPassword] = useState("");
  const [isChecked, setIschecked] = useState(false);
  const [isRedundancy, setisRedundancy] = useState(false);

  const navigate = useNavigate();

  // handler 
  const onEmailHandler = (event) => {
    setEmail(event.currentTarget.value);
    setIschecked(false);
  };

  const onNameHandler = (event) => {
    setName(event.currentTarget.value);
  };

  const onPasswordHandler = (event) => {
    setPassword(event.currentTarget.value);
  };

  const onConfirmPasswordHandler = (event) => {
    setConfirmPassword(event.currentTarget.value);
  };

  const onEmailRedundancyCheck = (event) => {
    event.preventDefault();
    fetch(
      "https://server-real.herokuapp.com/userinfo"
      //"http://localhost:4000/userinfo"
    )
      .then((response) => response.json())
      .then((res) => {
        for (let i = 0; i < res.length; i++) {
          if (res[i]["email"] === Email) {
            setisRedundancy(true);
            return alert("   !");
          } else {
            setisRedundancy(false);
          }
        }
        if (!isRedundancy) alert("  !");
        setIschecked(true);
      });
  };

  const onSubmitHandler = (event) => {
    //       .
    event.preventDefault();

    if (Email === "") {
      return alert(" .");
    } else if (!isChecked) {
      return alert("   .");
    } else if (isRedundancy) {
      return alert("  .");
    } else if (Name === "") {
      return alert(" .");
    } else if (Password === "") {
      return alert(" .");
    } else if (Password !== ConfirmPassword) {
      return alert("   .");
    }

    let body = {
      email: Email,
      name: Name,
      password: Password,
    };

    fetch(
      "https://server-real.herokuapp.com/register",
      //"http://localhost:4000/register",
      {
        method: "post", // 
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify(body),
      }
    );

    alert("  .");

    navigate("/");
  };

  return (
    <div className="real-background">
      <div className="center-box">
        <form
          style={{ display: "flex", flexDirection: "column" }}
          onSubmit={onSubmitHandler}
        >
          <h2 className="header-text"></h2>
          <input
            type="email"
            value={Email}
            onChange={onEmailHandler}
            className="input-box"
          />
          <div className="blank-box"></div>
          <button
            type="submit"
            onClick={onEmailRedundancyCheck}
            className="register-redundancy-button"
          >
              
          </button>
          <div className="blank-box"></div>
          <h2 className="header-text"></h2>
          <input
            type="text"
            value={Name}
            onChange={onNameHandler}
            className="input-box"
          />
          <div className="blank-box"></div>
          <h2 className="header-text"></h2>
          <input
            type="password"
            value={Password}
            onChange={onPasswordHandler}
            className="input-box"
          />
          <div className="blank-box"></div>
          <h2 className="header-text"> </h2>
          <input
            type="password"
            value={ConfirmPassword}
            onChange={onConfirmPasswordHandler}
            className="input-box"
          />
          <div className="blank-box"></div>
          <button type="submit" className="button">
             
          </button>
        </form>
      </div>
    </div>
  );
}

export default Register;
