import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import "../css/App.css";

function Login({ setLoginStatus }) {
  const [inputId, setInputId] = useState("");
  const [inputPw, setInputPw] = useState("");

  const navigate = useNavigate();

  // input data     value   useState 
  const handleInputId = (e) => {
    setInputId(e.target.value);
  };

  const handleInputPw = (e) => {
    setInputPw(e.target.value);
  };

  // login   
  const onClickLogin = () => {
    let body = {
      email: inputId,
      password: inputPw,
    };

    fetch(
      "https://server-real.herokuapp.com/login",
      //"http://localhost:4000/login",
      {
        method: "post", // 
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify(body),
      }
    )
      .then((response) => response.json())
      .then((res) => {
        if (res.message) {
          localStorage.setItem("ID", "");
          return alert("    .");
        } else {
          localStorage.setItem("ID", res[0]["id"]);
          localStorage.setItem("group_type", res[0]["group_type"]);
          return res[0]["group_type"];
        }
      })
      .then((id) => {
        if (id) {
          navigate(`/survey`);
        }
      });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
  };

  return (
    <div className="real-background">
      <div className="center-box">
        <h1 className="header-text">Login</h1>
        <form onSubmit={handleSubmit}>
          <div>
            <label htmlFor="input_id">Email : </label>
            <input
              type="text"
              name="input_id"
              value={inputId}
              onChange={handleInputId}
              className="login-input-box"
            />
          </div>
          <div className="blank-box"></div>
          <div>
            <label htmlFor="input_pw">PassWord : </label>
            <input
              type="password"
              name="input_pw"
              value={inputPw}
              onChange={handleInputPw}
              className="login-input-box"
            />
          </div>
          <div className="blank-box"></div>
          <div>
            <button
              type="button"
              onClick={onClickLogin}
              className="login-button"
            >
              login
            </button>
          </div>
          <div className="blank-box"></div>
        </form>
        <div>
          <Link to="/findpassword">
            <button type="button" className="login-fp-button">
              find password
            </button>
          </Link>
          <Link to="/register">
            <button type="button" className="login-register-button">
              register
            </button>
          </Link>
        </div>
      </div>
    </div>
  );
}

export default Login;
