import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import "../css/App.css";
import styled from "styled-components";

function SurverPage({ loginStatus }) {
  const [Age, setAge] = useState("");
  const [Gender, setGender] = useState("");
  const [Degree, setDegree] = useState("");
  const [EduBackground, setEduBackground] = useState("");
  //    
  const [checkItems, setCheckItems] = useState([]);
  const [major1, setMajor1] = useState("");
  const [major2, setMajor2] = useState("");
  const [major3, setMajor3] = useState("");
  const [workExperience, setWorkExperience] = useState("  ");

  const majorData = [
    { id: 0, title: "" },
    { id: 1, title: "/" },
    { id: 2, title: "" },
    { id: 3, title: "()" },
    { id: 4, title: "//" },
    { id: 5, title: "" },
    { id: 6, title: "" },
    { id: 7, title: "/" },
    { id: 8, title: "" },
  ];

  const navigate = useNavigate();

  const onAgeHandler = (event) => {
    setAge(event.currentTarget.value);
  };

  const onGenderHandler = (event) => {
    setGender(event.currentTarget.value);
  };

  const onDegreeHandler = (event) => {
    setDegree(event.currentTarget.value);
  };

  const onEduBackgroundHandler = (event) => {
    setEduBackground(event.currentTarget.value);
  };

  //   
  const handleSingleCheck = (checked, id) => {
    if (checked) {
      if (checkItems.length === 0) {
        setMajor1(majorData[id].title);
        setCheckItems((prev) => [...prev, id]);
      } else if (checkItems.length === 1) {
        setMajor2(majorData[id].title);
        setCheckItems((prev) => [...prev, id]);
      } else if (checkItems.length === 2) {
        setMajor3(majorData[id].title);
        setCheckItems((prev) => [...prev, id]);
      } else if (checkItems.length === 3) {
        alert("3  .");
      }
    } else {
      //         ()
      setCheckItems(checkItems.filter((el) => el !== id));
      if (checkItems.length === 1) {
        setMajor1("");
      } else if (checkItems.legnth === 2) {
        setMajor2("");
      } else if (checkItems.length === 3) {
        setMajor3("");
      }
    }
  };

  const onWorkExperienceHandler = (event) => {
    setWorkExperience(event.currentTarget.value);
  };

  const onSubmitHandler = (event) => {
    //       .
    event.preventDefault();

    if (Age === "") {
      return alert(" .");
    } else if (major1 === "") {
      return alert("    .");
    } else if (Degree === "") {
      return alert("  .");
    } else if (EduBackground === "") {
      return alert("  .");
    }

    let body = {
      id: localStorage.getItem("ID"),
      age: Age,
      gender: Gender,
      degree: Degree,
      eduBackground: EduBackground,
      major1: major1,
      major2: major2,
      major3: major3,
      workExperience: workExperience,
    };

    fetch(
      "https://server-real.herokuapp.com/survey",
      //"http://localhost:4000/survey",
      {
        method: "post", // 
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify(body),
      }
    );

    alert(" .");

    navigate("/survey2");
  };

  useEffect(() => {
    if (!localStorage.getItem("ID")) {
      alert(" !");
      navigate("/");
    }
  }, [loginStatus]);

  return (
    <div className="real-background-survey">
      <div className="survey-one-box">
        <h4 className="header-text">  ?</h4>
        <select value={Age} onChange={onAgeHandler} className="input-box">
          <option value="">-</option>
          <option value="10">10</option>
          <option value="20">20</option>
          <option value="30">30</option>
          <option value="40">40</option>
          <option value="50">50</option>
          <option value="60 ">60 </option>
        </select>
        <div className="blank-box"></div>
        <h4 className="header-text">  ?</h4>
        <select value={Gender} onChange={onGenderHandler} className="input-box">
          <option value=""></option>
          <option value=""></option>
        </select>
        <div className="blank-box"></div>
        <h4 className="header-text">
               .
          <br /> ( 3  )
        </h4>
        <StyledTable>
          <tbody>
            {majorData?.map((data, key) => (
              <tr key={key}>
                <td>
                  <input
                    type="checkbox"
                    name={`select-${data.id}`}
                    onChange={(e) =>
                      handleSingleCheck(e.target.checked, data.id)
                    }
                    //         ,   
                    checked={checkItems.includes(data.id) ? true : false}
                  />
                </td>
                <td className="second-row">{data.title}</td>
              </tr>
            ))}
          </tbody>
        </StyledTable>
        <div className="blank-box"></div>
        <h4 className="header-text">
             ? <br />
          (    .)
        </h4>
        <select
          value={workExperience}
          onChange={onWorkExperienceHandler}
          className="input-box"
        >
          <option value="  ">-</option>
          <option value="1 ">1 </option>
          <option value="1  ~ 3 ">1  ~ 3 </option>
          <option value="3  ~ 5 ">3  ~ 5 </option>
          <option value="5  ~ 10 ">5  ~ 10 </option>
          <option value="10 ">10 </option>
        </select>
        <div className="blank-box"></div>
        <h4 className="header-text"> </h4>
        <select value={Degree} onChange={onDegreeHandler} className="input-box">
          <option value="">-</option>
          <option value=""></option>
          <option value=""> </option>
          <option value=""> ( )</option>
          <option value=""></option>
          <option value=""></option>
        </select>
        <div className="blank-box"></div>
        <h4 className="header-text"> </h4>
        <select
          value={EduBackground}
          onChange={onEduBackgroundHandler}
          className="input-box"
        >
          <option value="">-</option>
          <option value=""></option>
          <option value=""></option>
          <option value=",">, </option>
          <option value=""></option>
          <option value=""></option>
        </select>
        <div className="blank-box"></div>

        <form
          style={{ display: "flex", flexDirection: "column" }}
          onSubmit={onSubmitHandler}
        >
          <button type="submit" className="button">
            
          </button>
        </form>
      </div>
    </div>
  );
}

export default SurverPage;

const StyledTable = styled.table`
  text-align: center;
  border-collapse: collapse;
  left: 25%;
  position: relative;
  thead {
    tr {
      th {
        padding: 10px 15px;
        background-color: #888;
        color: #fff;
        font-weight: 700;
      }
    }
  }
  tbody {
    tr {
      td {
        padding: 7px 15px;
        border-bottom: 1px solid #eee;
      }
    }
  }
  .second-row {
    width: 200px;
  }
`;
